import React, { useEffect } from "react";
import {
  Avatar,
  Button,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
} from "@mui/material";
import styles from "./Builder.module.css";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import SendIcon from "@mui/icons-material/Send";
import ImageIcon from "@mui/icons-material/Image";
import { styled } from "@mui/material/styles";
import { generateBookCover, getPublicUrl } from "../logic/canvas";

const VisuallyHiddenInput = styled("input")({
  clip: "rect(0 0 0 0)",
  clipPath: "inset(50%)",
  height: 1,
  overflow: "hidden",
  position: "absolute",
  bottom: 0,
  left: 0,
  whiteSpace: "nowrap",
  width: 1,
});

export const Builder = () => {
  const [file, setFile] = React.useState<File>();
  const [output, setOutput] = React.useState<File>();

  const handleSelectFile = (files: FileList) => {
    setFile(files[0]);
  };

  const make = () => {
    if (!file) {
      alert("No file is selected.");
      return;
    }
    generateBookCover(URL.createObjectURL(file)).then((output) => {
      setOutput(output);
    });
  };

  return (
    <div className={styles.Wrapper}>
      {output ? (
        <img src={URL.createObjectURL(output)} className={styles.Output} />
      ) : (
        <>
          <div className={styles.Container}>
            <p>Book Mockup</p>
            <Button
              component="label"
              role={undefined}
              variant="contained"
              tabIndex={-1}
              startIcon={<CloudUploadIcon />}
            >
              Upload file
              <VisuallyHiddenInput
                type="file"
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  if (e.target.files) handleSelectFile(e.target.files);
                }}
              />
            </Button>
            {file && (
              <List
                sx={{
                  width: "100%",
                  maxWidth: 360,
                  bgcolor: "background.paper",
                }}
              >
                <ListItem>
                  <ListItemAvatar>
                    <Avatar>
                      <ImageIcon />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={file?.name.split(".")[0]}
                    secondary={file?.name.split(".")[1]}
                  />
                </ListItem>
              </List>
            )}
            <Button
              variant="contained"
              endIcon={<SendIcon />}
              sx={{
                marginTop: 2,
              }}
              onClick={make}
            >
              Build
            </Button>
          </div>
        </>
      )}
    </div>
  );
};
